:root {
  font-family: "Microsoft YaHei", "PingFang SC", system-ui, sans-serif;
  color: #1f2933;
  background: #f4f6fb;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 32px;
}

.app {
  max-width: 1200px;
  margin: 0 auto 48px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  background: linear-gradient(135deg, #6d5dfc, #b76cfd);
  color: #fff;
  border-radius: 18px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
}

h1 {
  margin: 0 0 8px;
  font-size: 28px;
}

.ghost-btn {
  border: 1px solid rgba(255, 255, 255, 0.6);
  background: transparent;
  color: #fff;
  padding: 10px 18px;
  border-radius: 999px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.ghost-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.panel {
  background: #fff;
  border-radius: 18px;
  padding: 24px;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.08);
}

.panel-title {
  font-weight: 600;
  margin-bottom: 16px;
}

.upload-row {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: center;
}

.hint {
  font-size: 13px;
  color: #6b7280;
}

input[type="number"] {
  width: 80px;
}

.settings {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
  align-items: center;
}

label {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  gap: 6px;
  color: #4b5563;
}

button {
  background: #4f46e5;
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 10px 16px;
  cursor: pointer;
  transition: opacity 0.2s ease;
}

button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

button:not(:disabled):hover {
  opacity: 0.9;
}

.legend {
  display: flex;
  gap: 24px;
  margin-top: 16px;
  font-size: 14px;
  color: #4b5563;
}

.badge {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 4px;
  margin-right: 6px;
}

.badge.male {
  background: #60a5fa;
}

.badge.female {
  background: #f472b6;
}

.badge.empty {
  background: #cbd5f5;
}

.layout-panel {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.grid-wrapper {
  display: grid;
  grid-template-columns: 80px minmax(200px, auto);
  grid-template-rows: 40px auto;
  gap: 12px;
  align-items: start;
}

.grid-corner {
  grid-column: 1;
  grid-row: 1;
}

.col-headers {
  grid-column: 2;
  grid-row: 1;
}

.row-headers {
  grid-column: 1;
  grid-row: 2;
}

.seat-grid {
  grid-column: 2;
  grid-row: 2;
}

.grid-wrapper.inactive .row-headers,
.grid-wrapper.inactive .col-headers {
  visibility: hidden;
}

.grid-corner {
  align-self: end;
  font-size: 12px;
  color: #94a3b8;
}

.row-headers,
.col-headers {
  display: grid;
  gap: 10px;
}

.row-headers {
  grid-auto-rows: minmax(40px, auto);
}

.col-headers {
  grid-auto-columns: minmax(40px, auto);
  text-align: center;
}

.header-item {
  background: #eef2ff;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  font-weight: 600;
  color: #4338ca;
}

.seat-grid {
  min-height: 220px;
  border: 2px dashed #cbd5f5;
  border-radius: 14px;
  padding: 16px;
  display: grid;
  gap: 10px;
  justify-content: flex-start;
  align-content: flex-start;
  background: #f8f9ff;
  transition: border-color 0.2s ease;
}

.seat-grid.empty {
  align-items: center;
  justify-items: center;
  justify-content: center;
  align-content: center;
  text-align: center;
  color: #9ca3af;
}

.seat {
  border-radius: 12px;
  padding: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4px;
  text-align: center;
  font-size: 13px;
  background: #eef2ff;
  border: 2px solid transparent;
  transition: transform 0.1s ease, border 0.2s ease, box-shadow 0.2s ease;
}

.seat .position {
  font-size: 12px;
  color: #6b7280;
}

.seat .serial {
  font-size: 12px;
  font-weight: 700;
  color: #4338ca;
}

.seat.male {
  background: rgba(96, 165, 250, 0.2);
  border-color: rgba(59, 130, 246, 0.5);
}

.seat.female {
  background: rgba(244, 114, 182, 0.2);
  border-color: rgba(236, 72, 153, 0.5);
}

.seat.empty {
  background: rgba(203, 213, 245, 0.3);
  border-color: transparent;
}

.seat.selected {
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.4);
}

.seat .name {
  font-weight: 600;
}

.seat .sid {
  font-size: 12px;
  color: #4b5563;
}

.table-wrapper {
  overflow: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  border-bottom: 1px solid #e5e7eb;
  padding: 10px;
  text-align: left;
}

th {
  background: #f3f4f6;
}

.hidden {
  display: none;
}

@media (max-width: 768px) {
  body {
    padding: 16px;
  }

  header {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }

  .settings,
  .actions,
  .upload-row {
    flex-direction: column;
    align-items: stretch;
  }

  input[type="number"] {
    width: 100%;
  }
}

